.modal {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  text-align: center; }

/* Modal Header */
.modal-header {
  padding: 2px 16px;
  background-color: #13253a;
  color: white; }

/* Modal Body */
.modal-body {
  padding: 20px 15px; }

/* Modal Content */
.modal-content {
  background-color: #13253a;
  margin: auto;
  padding: 0;
  width: 80%;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  -webkit-animation-name: animatetop;
          animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s; }

/* Add Animation */
@-webkit-keyframes animatetop {
  from {
    top: -300px;
    opacity: 0; }
  to {
    top: 0;
    opacity: 1; } }
@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0; }
  to {
    top: 0;
    opacity: 1; } }

